<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:margin-left="http://www.w3.org/1999/xhtml"
          xmlns:margin-right="http://www.w3.org/1999/xhtml">

  <div id="app">

    <Layout>
      <Header>


        <h2 id="h2_title"><img src="./assets/logo.png" :width="40" :height="40"/>有恒群机器人管理系统</h2>

      </Header>
      <Content>
        <Tabs value="0" @on-click="tabClick" size="small" v-model="witch" :animated="false">

          <TabPane label="早安" name="0">

            <Button type="primary" size="large" @click="getData">刷新</Button>
            <Button type="primary" size="large" @click="addMorning">添加</Button>

            <Table stripe :loading="loading" height="600" :columns="position_col" :data="morning_data"
                   ref="table_positions"></Table>

          </TabPane>

          <TabPane label="晚安" name="1">
            <Button type="primary" size="large" @click="getData">刷新</Button>
            <Button type="primary" size="large" @click="addNight">添加</Button>
            <Table stripe :loading="loading" height="600" :columns="position_col" :data="night_data"
                   ref="table_positions"></Table>
          </TabPane>

          <TabPane label="问答" name="2">
            <Button type="primary" size="large" @click="getData">刷新</Button>
            <Button type="primary" size="large" @click="addQuestion">添加</Button>
            <Table stripe :loading="loading" height="600" :columns="question_col" :data="question_data"
                   ref="table_positions"></Table>
          </TabPane>

        </Tabs>

        <!--<Button type="primary" size="large" @click="exportData(3)">-->
        <!--<Icon type="ios-download-outline"></Icon>-->
        <!--导出-->
        <!--</Button>-->
      </Content>
      <Footer>©有恒科技</Footer>
    </Layout>


    <!--<HelloWorld/>-->
    <!--<ul>-->
    <!--<li v-for="todo in todos">-->
    <!--<span>{{ todo.text }}</span>-->
    <!--</li>-->
    <!--</ul>-->
    <Modal
      v-model="modal6"
      title="提示"
      :loading="modal6loading"
      @on-ok="confirmDelete">
      <p>您确定要删除这条记录吗？</p>
    </Modal>

  </div>

</template>

<script>
  // import HelloWorld from './components/HelloWorld'
  export default {
    name: 'App',
    data() {
      return {
        deleteIndex: 0,
        witch: "0",
        token: localStorage.getItem('youheng.leefeng.top'),
        modal6: false,
        modal6loading: false,
        loading: true,
        newTodo: '',
        todos: [{
          text: '添加项目 1'
        }],
        position_col: [
          {
            title: 'id',
            key: 'uuid',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', {}, params.index + 1)
            },

          }, {
            title: '内容',
            key: 'text'
          },
          {
            title: '是否已发送',
            key: 'isSend',
            width: 100,
            align: 'center',
            render: (h, params) => {
              let t = '否';
              if (this.getValue(params.index)['isSend']) {
                t = '是';
              }

              return h('span', {}, t)
            }
          },
          {
            title: '发送时间',
            key: 'time_send',
            width: 200,
            align: 'center',
            render: (h, params) => {
              let time = this.getValue(params.index)['time_send'];
              time = this.goodTime(time);
              return h('span', {}, time)
            },

          },
          {
            title: '创建时间',
            key: 'time_creat',
            width: 200,
            align: 'center',
            render: (h, params) => {

              return h('div', {}, this.goodTime(this.getValue(params.index)['time_creat']));
            },

          },
          // {
          //   title: '早安吗',
          //   key: 'type'
          // },
          {
            title: '操作',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {


                      this.showEditAlert(params.index)


                    }
                  }
                }, '编辑'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.deleteIndex = params.index;
                      this.modal6 = true
                    }
                  }
                }, '删除')])
            }
          }
        ],
        question_col: [{
          title: 'id',
          key: 'uuid',
          width: 60,
          align: 'center',
          render: (h, params) => {
            return h('span', {}, params.index + 1)
          }
        }, {
          title: '问题',
          key: 'question',
          width: 300

        }, {
          title: '答案',
          key: 'result'

        }, {
          title: '图片',
          key: 'image',
          width: 200
        }, {
          title: '操作',
          key: 'action',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.showQuestionEditAlert(params.index)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.deleteIndex = params.index;
                    this.modal6 = true
                  }
                }
              }, '删除')])
          }
        }],
        morning_data: [],
        night_data: [],
        question_data: []
      }
    },
    value: "",
    password: "",
    title: "",
    image: "",
    components: {
      // HelloWorld
    },
    sockets: {
      yh_connect(msg) {
        this.info = JSON.parse(msg)
        // if (typeof (msg) === 'undefined') {
        //   return
        // }
        console.log(msg);
        this.todos.push({
          text: msg
        });
        this.$Message.success('连接成功');
        this.getAllPositionDetails(true);
        // setInterval(() => {
        //   this.getAllPositionDetails(false);
        // }, 5000)
      },
      yh_positions_detail(msg) {
        let m = JSON.parse(msg);
        this.position_data = m.data;
        if (m.clientMessage) {
          this.$Message.success('获取持仓信息成功');
        }

      }
    },
    mounted() {
      this.init()
    },
    methods: {
      getValue(i) {
        if (this.witch === "0") {
          return this.morning_data[i];

        } else if (this.witch === "1") {
          return this.night_data[i];
        } else if (this.witch === "2") {
          return this.question_data[i];
        }
      },
      showEditAlert(i) {
        let place = '输入早安内容';
        let uuid = "";
        switch (this.witch) {
          case "0":
            this.value = this.night_data[i]['text'];
            place = '输入晚安内容';
            uuid = this.night_data[i]['uuid'];
            break;
          case "1":
            this.value = this.morning_data[i]['text'];
            uuid = this.morning_data[i]['uuid'];
            break;

        }

        this.$Modal.confirm({
          render: (h) => {
            return h('Input', {
              props: {
                value: this.value,
                autofocus: true,
                placeholder: place,
                type: 'textarea',

                rows: 6
              },
              on: {
                input: (val) => {
                  this.value = val;
                }
              }
            })
          },

          onOk: () => {
            let postData = this.$qs.stringify({
              text: this.value,
              uuid: uuid
            });

            switch (this.witch) {
              case "2":
                this.$http.put('/question', postData)
                  .then((response) => {

                    if (response.data.code === 200) {
                      this.$Message.success(response.data.msg);
                      this.getData()
                    } else {
                      this.$Message.error(response.data.msg);
                    }
                  }).catch((error) => {
                  this.$Message.error('修改失败');
                });
                break;
              default:

                this.$http.put('/duyao', postData)
                  .then((response) => {

                    if (response.data.code === 200) {
                      this.$Message.success(response.data.msg);
                      this.getData()
                    } else {
                      this.$Message.error(response.data.msg);
                    }
                  }).catch((error) => {
                  this.$Message.error('修改失败');
                });
                break;
            }
            this.value = ""
          }
        })
      },
      showQuestionEditAlert(i) {

        this.value = this.question_data[i]['result'];
        let uuid = this.question_data[i]['uuid'];
        this.title = this.question_data[i]['question'];
        this.image = this.question_data[i]['image'];
        this.$Modal.confirm({
          render: (h) => {
            return h('div', [
              h('Input', {
                props: {
                  value: this.title,
                  autofocus: true,
                  placeholder: '输入问题内容',
                  type: 'textarea',
                  rows: 2
                },

                on: {
                  input: (val) => {
                    this.title = val;
                  }
                }
              }), h('br', {}), h('br', {}), h('Input', {
                props: {
                  value: this.value,
                  autofocus: true,
                  placeholder: '输入答案内容',
                  type: 'textarea',
                  rows: 6
                },

                on: {
                  input: (val) => {
                    this.value = val;
                  }
                }
              }), h('br', {}), h('br', {}), h('Input', {
                props: {
                  value: this.image,
                  autofocus: true,
                  placeholder: '输入图片名称'
                },

                on: {
                  input: (val) => {
                    this.image = val;
                  }
                }
              })
            ]);
          },
          onOk: () => {
            console.log(this.value);
            let postData = this.$qs.stringify({
              question: this.title,
              result: this.value,
              image: this.image,
              uuid: uuid
            });

            this.$http.put('/question', postData)
              .then((response) => {
                this.$Message.success('修改成功');
                this.getData();

              }).catch((error) => {
              console.log(error);
              this.$Message.success('修改失败');
            });
            this.value = "";
            this.title = "";
            this.image = '';
          }
        })

      },
      confirmDelete() {
        let uuid = this.getValue(this.deleteIndex)['uuid']

        this.modal6loading = true;
        switch (this.witch) {
          case "2":
            this.$http.delete('/question?uuid=' + uuid)
              .then((response) => {
                this.remove(this.deleteIndex);
                this.$Message.success('删除成功');
                this.modal6loading = false
              })
              .catch((error) => {
                this.$Message.success('删除失败');
                this.modal6loading = false
              })
            break;
          default:
            this.$http.delete('/duyao?uuid=' + uuid)
              .then((response) => {
                this.remove(this.deleteIndex);
                this.$Message.success('删除成功');
                this.modal6loading = false
              })
              .catch((error) => {
                this.$Message.success('删除失败');
                this.modal6loading = false
              });
            break;
        }

      },
      remove(index) {
        switch (this.witch) {
          case "0":
            this.morning_data.splice(index, 1);
            break;
          case "1":
            this.night_data.splice(index, 1);
            break;
          case "2":
            this.question_data.splice(index, 1);
            break;
        }


      },
      init() {
        this.getData()
      },

      addMorning() {
        this.$Modal.confirm({
          render: (h) => {
            return h('Input', {
              props: {
                value: this.value,
                autofocus: true,
                placeholder: '输入早安内容',
                type: 'textarea',
                rows: 6
              },
              on: {
                input: (val) => {
                  this.value = val;
                }
              }
            })
          },
          onOk: () => {
            console.log(this.value);
            let postData = this.$qs.stringify({
              text: this.value,
              type: 0
            });

            this.$http.post('/duyao', postData)
              .then((response) => {
                if (response.data.code == 200) {
                  this.$Message.success(response.data.msg);
                  this.getData()
                } else {
                  this.$Message.error(response.data.msg);
                }


              }).catch((error) => {
              this.$Message.error('添加失败');
            });
            this.value = ""
          }
        })
      },
      addNight() {
        this.$Modal.confirm({
          render: (h) => {
            return h('Input', {
              props: {
                value: this.value,
                autofocus: true,
                placeholder: '输入晚安内容',
                type: 'textarea',
                rows: 6
              },

              on: {
                input: (val) => {
                  this.value = val;
                }
              }
            })
          },
          onOk: () => {
            console.log(this.value);
            let postData = this.$qs.stringify({
              text: this.value,
              type: 1
            });

            this.$http.post('/duyao', postData)
              .then((response) => {
                this.$Message.success('添加成功');
                this.getData();

              }).catch(function (error) {
              this.$Message.success('添加失败');
            });
            this.value = ""
          }
        })
      },
      addQuestion() {
        this.$Modal.confirm({
          render: (h) => {
            return h('div', [
              h('Input', {
                props: {
                  value: this.title,
                  autofocus: true,
                  placeholder: '输入问题内容',
                  type: 'textarea',
                  rows: 2
                },

                on: {
                  input: (val) => {
                    this.title = val;
                  }
                }
              }), h('br', {}), h('br', {}), h('Input', {
                props: {
                  value: this.value,
                  autofocus: true,
                  placeholder: '输入答案内容',
                  type: 'textarea',
                  rows: 6
                },

                on: {
                  input: (val) => {
                    this.value = val;
                  }
                }
              }), h('br', {}), h('br', {}), h('Input', {
                props: {
                  value: this.image,
                  autofocus: true,
                  placeholder: '输入图片名称'
                },

                on: {
                  input: (val) => {
                    this.image = val;
                  }
                }
              })
            ]);
          },
          onOk: () => {
            console.log(this.value);
            let postData = this.$qs.stringify({
              question: this.title,
              result: this.value,
              image: this.image
            });

            this.$http.post('/question', postData)
              .then((response) => {
                this.$Message.success('添加成功');
                this.getData();

              }).catch((error) => {
              console.log(error);
              this.$Message.success('添加失败');
            });
            this.value = "";
            this.title = "";
            this.image = '';
          }
        })
      },
      showLogin() {
        this.$Modal.confirm({
          title: '登录',
          okText: '登录',
          render: (h) => {

            return h('div', {}, [h('br'), h('Input', {
              props: {
                value: this.value,
                autofocus: true,
                placeholder: '请输入手机号码'
              },
              on: {
                input: (val) => {
                  this.value = val;
                }
              }
            }), h('br'),h('br'), h('Input', {
              props: {
                value: this.password,
                autofocus: true,
                placeholder: '请输入密码'
              },
              on: {
                input: (val) => {
                  this.password = val;
                }
              }
            })]);

          },
          onOk: () => {

            this.$http.get('/user/login?phone=' + this.value)
              .then((response) => {
                this.value = '';
                if (response.data.code == 200) {

                  this.token = response.data.data.token;
                  localStorage.setItem('youheng.leefeng.top', this.token);
                  this.getData()
                } else {
                  console.log(response.data);
                  this.$Message.error(response.data.msg);
                }


              }).catch((error) => {
              this.$Message.success(error.message);
            });
            this.value = "";
            this.password = '';
          }

        })
      },

      getData() {
        this.loading = true;
        switch (this.witch) {
          case "0":
            this.$http.get('/duyao?isM=0')
              .then((response) => {
                this.morning_data = response.data.data;
                this.loading = false
                // if (m.clientMessage) {
                //   this.$Message.success('获取数据成功');
                // }
              })
              .catch((error) => {
                console.log(error);
                this.loading = false;
                this.$Message.error('获取数据失败');
                this.showLogin()
              });
            break;
          case "1":
            this.$http.get('/duyao?isM=1')
              .then((response) => {
                this.night_data = response.data.data;
                this.loading = false;
                // if (m.clientMessage) {
                this.$Message.success('获取数据成功');
                // }
              })
              .catch((error) => {
                console.log(error);
                this.loading = false;
                this.$Message.error(error.message);
                this.showLogin();
              });
            break;
          case "2":
            this.$http.get('/question')
              .then((response) => {
                this.question_data = response.data.data;
                this.loading = false;
                // if (m.clientMessage) {
                this.$Message.success('获取数据成功');
                // }
              })
              .catch((error) => {
                console.log(error);
                this.loading = false;
                this.$Message.error(error.message);
                this.showLogin();
              });
            break;
        }


      },

      tabClick(name) {
        switch (this.witch) {
          case "0":
            if (this.morning_data.length === 0) {
              this.getData();
            }
            break;
          case "1":
            if (this.night_data.length === 0) {
              this.getData()
            }
            break;
          case "2":
            if (this.question_data.length === 0) {
              this.getData();
            }
            break;
        }
      },
      getAllPositionDetails(showMessage) {
        this.$socket.emit('getAllPositionDetails', showMessage)
      },
      exportData(type) {
        if (type === 3) {
          this.$refs.table_positions.exportCsv({
            filename: 'Custom data',
            columns: this.position_col.filter((col, index) => index < 4),
            data: this.position_data.filter((data, index) => index < 4)
          });
        }
      },

      goodTime(str) {

        let oldTime = new Date(str);
        if (oldTime.getFullYear() < 2018) {
          return '';
        }
        return oldTime.getFullYear() + '-' + (oldTime.getMonth() + 1) + '-' + oldTime.getDate() + ' ' + oldTime.getUTCHours() + ':' + oldTime.getMinutes() + ':' + oldTime.getSeconds();
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: left;
    color: #2c3e50;
    margin: 0px;
  }

  #h2_title {
    color: white;
  }
</style>



