import Vue from 'vue'
import App from './App'
import VueSocketio from 'vue-socket.io'
import iView from 'iview';
import 'iview/dist/styles/iview.css';
import axios from 'axios'
import qs from 'qs';
Vue.config.productionTip = false;
// Vue.use(VueSocketio, 'http://127.0.0.1:5001/')
Vue.use(iView);

// axios.defaults.baseURL = 'http://0.0.0.0:5001/';
// axios.defaults.baseURL = 'http://youheng.leefeng.top/';
axios.interceptors.request.use(
  request =>{
    request.headers['Authorization'] = localStorage.getItem('youheng.leefeng.top');
    request.headers['Apptype'] = 'IOS';
    return request
  }
);
Vue.prototype.$http = axios;
Vue.prototype.$qs = qs;
/* eslint-disable no-new */
new Vue({
  el: '#app',

  render: h => h(App)

});



